.search {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    box-sizing: border-box;
    position: relative;
    margin: 0;
    padding: 0;

    width: 100%;
    max-height: 576px;
    /* overflow: auto; */
    background-image: url(../images/imagem_fundo.jpg);
    background-size: cover;
    /* background-position: top; */
}

.search__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    position: relative;
    margin: 0;
    padding: 0;
    gap: 64px;
    padding: 80px 0px;
    max-width: 702px;
    width: 100%;
}

.search__texts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 32px;
}

.search__title {
    margin: 0;
    padding: 0;
    font-family: Roboto Slab;
    font-size: 60px;
    font-weight: 400;
    line-height: 64px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: white;
}

.search__subtitle {
    margin: 0;
    padding: 0;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    color: white;
    max-width: 608px;
}


@media (min-width: 320px){

    .search__container {
