        padding: 0;
        gap: 86px;
        padding: 30px 0px;
        min-width: 285px;
        width: 100%;
        padding-left: 19px;
        padding-right: 19px;
    }

    .search__texts {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        gap: 11px;
    }
    
    .search__title {
        margin: 0;
