.article {
    border-radius: 16px;
    width: 400px;
    height: 576px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 25px 0px #00000026;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
  }
  
  .article__delete-button_visible {
    display: block;
    box-sizing: border-box;
    width: 18px;
    height: 19.3px;
    background-color: transparent;
    background-image: url(../images/Trash.svg);
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    cursor: pointer;
    padding: 0;
    margin-top: 18px;
    border: 0;
    margin-right: 15px;
    align-self: flex-end;
    z-index: 2;
  }
  
  .article__save-button {
    display: block;
    box-sizing: border-box;
    position: absolute;
    cursor: pointer;
    padding: 0;
    margin-top: 24px;
    border: 0;
    margin-right: 24px;
    align-self: flex-end;
    z-index: 2;
  
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 8px;
    background-image: url('../images/bookmark.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-size: 24px 24px;
    background-position: center;
  }
  
  .article__save-button:hover {
    background-image: url('../images/bookmark_black.png');
  }
  
  .article__save-message {
    position: absolute;
    z-index: 2;
    align-self: flex-end;
    margin-top: 24px;
    right: 66px;
  
    width: 182px;
    height: 40px;
    border-radius: 10px;
    box-shadow: 0px 10px 22px 0px #3B4A741A;
    background-color: white;
    color: black;
    margin-right: 5px;
    border: 0;
  }
  
  
  .article__delete-button_hidden {
    display: none;
  }
  
  .article__delete-button_visible:hover{
    opacity: 60%;
  }
  
  .article__image {
    width: 400px;
    height: 272px;
    max-height: 272px;
    object-fit: cover;
    border-radius: 16px 16px 0 0; 
    object-fit: cover;
  }
  
  .article__tag {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 400px;
    height: 284px;
    margin: 0 auto;
    /* vertical-align: middle; */
    padding: 0 24px 24px 24px;
  }
  
  .article__tag-date {
    font-family: Source Sans Pro;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    
    margin: 0;
    padding: 0;
    color: #B6BCBF;
    margin-bottom: 12px;
  }
  
  .article__tag-title {
    font-family: Roboto Slab;
    font-size: 26px;
    font-weight: 400;
    line-height: 30px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
  
    max-height: 90px;
  
    margin: 0;
    padding: 0;
    color: #000000;
    margin-bottom: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .article__tag-description {
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
  
    max-height: 88px;
  
    margin: 0;
    padding: 0;
    color: #000000;  
    margin-bottom: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .article__tag-source {
    font-family: Roboto Slab;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0.4000000059604645px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
  
    margin: 0;
    padding: 0;
    color: #B6BCBF; 
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: auto;
  }
  
  .article__tag-likes {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 246px;
    margin: 0 auto;
    width: 21px;
    height: 37px;
    gap: 3px;
  }
  
  .article__tag-like {
    padding: 0;
    border: 0;
    margin: 0;
    background-color: transparent;
    background-image: url("../images/Like.svg");
    width: 21px;
    height: 19px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .article__tag-like:hover {
    opacity: 50%;
  }
  
  .article__tag-like_liked {
    background-image: url("../images/Union.png");
  }
  
  .article__tag-like-count {
    font-family: "Inter";
    font-size: 13px;
    font-weight: 400;
    line-height: 15.73px;
    text-align: center;
    margin: 0;
    padding: 0;
  }








